import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { UserService } from 'src/app/shared/Service/userService/user.service';
import { ToasterService } from 'src/app/shared/Module/toaster/toaster.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  formGroup: FormGroup;
  formGroupP: FormGroup;
  currentLanguage="en";
  LanguageImage={
    en:"assets/img/country/us-flag.png",
    ch:"assets/img/country/china-flag.png",
  };
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void {
    this.formGroup = this._formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
      role: ['user', Validators.required],
    });
    this.formGroupP = this._formBuilder.group({
      name: [''],
      mail: ['', [Validators.required,Validators.email]],
      phone: [''],
      message: ['请重设我的密码'],
    });
  }
   //LanguageChagePart
    changeLang(language: string) {
      this.currentLanguage=language;
      localStorage.setItem('language',language)
      this.translate.use(language);
    }
    passwordReset(){
      console.log('dd')
      console.log(this.formGroupP.value)
      this.userService.postRequest("api/sendGmail",this.formGroupP.value,false).subscribe(
        res=>{
          this.Toaster('success','','Your message have sent successfully. Please wait the result.',2000); 
        },
        err=>{
          this.handleError(err)
        }
      )
    }
  login(){
    console.log(this.formGroup.value)
    // alert('My skype address is devstar1992@outlook.com.Please do not say about skype word')
    if(!this.formGroup.invalid){
      this.userService.postRequest("api/login",this.formGroup.value,false).subscribe(
        res=>{
          this.userService.setToken(
            {
              token:res['token'],
              userInfo:res['userInfo'],
              expiresAt:res['expiresAt']
            }
          );
          this.Toaster('success','','Welcome'); 
          this.userService.gotoFirstPage()
        },
        err=>{
           this.handleError(err)
        }
      )
    }
  }
  //toastr functions
  handleError(err){
    if(err.status==504)  this.Toaster('error', '', 'server is not responsing.',4000);
    else         this.Toaster('error', '', err.error.message,1500);
   }
  Toaster(style,msgtopic,msgContent,time=1000) {
    this.toastr.show(style, msgtopic, msgContent, time);
}
}
