import { AuthInterceptor } from './auth/auth.interceptor';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from "@angular/router";
@Injectable({
  providedIn: 'root'
})
export class UserService {
  loginURL = '/login';
  noAuthHeader = { headers: new HttpHeaders({ 'NoAuth': 'True' }) };

  constructor(
    private http: HttpClient,
    private router: Router,
  ) { }
  //For login & register
  postRequest(URL, data = {}, header = true) {
    if (header) return this.http.post(URL, data, this.getHeader())
    else return this.http.post(URL, data, this.noAuthHeader)
  }
  getRequest(URL, header = false) {
    if (header) return this.http.get(URL,this.getHeader())
    else return this.http.get(URL,this.noAuthHeader)
  }
  logOut(){
    this.deleteToken();
    this.gotoHome();
  }
  gotoHome(){
    this.router.navigateByUrl('http://vdms.aidetecting.com/');
  }
  gotoFirstPage() {
    this.router.navigateByUrl('/map/monitor');
  }
  setToken(token) {
    localStorage.setItem('token', JSON.stringify(token));
  }
  getToken() {
    return JSON.parse(localStorage.getItem('token'));
  }
  deleteToken() {
    localStorage.removeItem('token');
  }
  getUserPayload() {
    var token = this.getToken();
    if (token) {
      var userPayload = atob(token['token'].split('.')[1]);
      return JSON.parse(userPayload);
    }
    else
      return null;
  }
  isLoggedIn() {
    var userPayload = this.getUserPayload();
    if (userPayload)  return userPayload.exp > Date.now() / 1000
    else return false;
  }
  getHeader() {
    if(!this.getToken()) {
      this.router.navigateByUrl(this.loginURL);
      return;
    }
    else if(!this.isLoggedIn()){
      this.router.navigateByUrl(this.loginURL);
      return;
    }
    const header = { headers: { Authorization: `Bearer ${this.getToken().token}` }, user: this.getToken().userInfo }
    return header;
  }
}
